import { Component, Input, forwardRef ,HostBinding,
  OnChanges,
  SimpleChanges,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  ViewChild,
  ElementRef,
  AfterViewInit,
  Output,
  EventEmitter} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'counter-input',
  template: '<button type="button" (click)="increment()">+</button>{{counterValue}}<button type="button" (click)="decrement()">-</button>',
  providers: [
    { provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CounterInputComponent),
      multi: true
    }
  ],
  styles: [':host { background-color: aquamarine;']
  
})
export class CounterInputComponent implements ControlValueAccessor
{
  //onChange: (val) => void;
  //onTouch: () => void;

  @Input()
  counterValue = 0;

  writeValue(value: any) {
    this.counterValue = value;
  }

  propagateChange = (_: any) => {};

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}


  increment() {
    this.counterValue++;
    this.propagateChange(this.counterValue);
  }

  decrement() {
    this.counterValue--;
    this.propagateChange(this.counterValue);
  }
}